import modalComponent from '@/components/modalComponent.vue'
import bannerComponent from '@/components/bannerComponent.vue';
import btnComponent from '@/components/btnComponent.vue';
import listItemComponent from '@/components/listItemComponent.vue';
export default {
	components: {
		bannerComponent,
		btnComponent,
		listItemComponent,
		modalComponent,
	},
	data() {
		return {
			theme: this.$app.getData('theme') || 0,
			modal: 'indexBanner',
			showBottomLoading: true,

			page: 1, // 当前页码
			keywords: '', // 关键字搜索
			rankField: 'week_hot', // 排名类别
			sign: 0, // 标记
			rankList: this.$app.getData('index_rankList') || [], // 排名列表

			topImg: {}, // 风云榜
			// bannerList: [],

			cutOffDate: '', // 截止日期
			// muti: false,
		};
	},

	onLoad(option) {
		this.getSunday()

		if (option.path) {
			return this.$app.goPage(option.path)
		}
		// 跳转到明星页
		if (option.starid) {
			this.starid = option.starid
			return this.goGroup(this.starid)
		}
	},
	onShow() {
		this.page = 1
		this.keywords = ''
		this.getRankList()
		this.getBannerList()
	},
	onShareAppMessage() {
		return this.$app.commonShareAppMessage()
	},
	onHide() {
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 0,
		})
	},
	/**
	 * 上拉加载
	 */
	onReachBottom() {
		if (!~this.rankField.indexOf('last')) {
			this.page++
			this.getRankList()
		}
	},
	methods: {
		/**
		 * 去圈子
		 */
		goGroup(starid) {





			if (this.$app.getData('userStar')['id'] == starid) {
				this.$app.goPage('/pages/group/group')
			} else {
				this.$app.goPage('/pages/group/star?starid=' + starid)
			}
			return
		},
		getLast() {
			var date = new Date();
			var currentMonth = date.getMonth();
			var nextMonth = ++currentMonth;
			var nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1);
			var oneDay = 1000 * 60 * 60 * 24;
			let dateTime = new Date(nextMonthFirstDay - oneDay);

			this.cutOffDate = dateTime.getMonth() + 1 + '月' + dateTime.getDate() + '日'
		},
		getSunday() {
			const time = new Date()
			const day = time.getDay() || 7
			time.setDate(time.getDate() - day + 7)
			this.cutOffDate = (time.getMonth() + 1) + '月' + time.getDate() + '日'
		},
		changeField(field) {
			this.page = 1
			this.rankField = field
			this.keywords = ''
			this.getRankList()
		},
		searchInput(e) {
			if (!this.keywords || !e.detail.value) this.rankList = []
			this.page = 1
			this.sign = 0
			this.rankField = 'week_hot'
			this.keywords = e.detail.value
			this.getRankList()
		},

		getBannerList() {
			this.$app.request('banner/top', {}, res => {
				this.topImg = res.data
			})
		},
		getRankList() {
			this.$app.request(this.$app.API.STAR_RANK, {
				page: this.page,
				rankField: this.rankField,
				keywords: this.keywords,
				sign: this.sign,
			}, res => {
				if (this.rankField != 'fengyun') {
					if (res.data.length < 10) {
						// 内容不足隐藏loadicon
						this.showBottomLoading = false
					}

					const rankList = []
					res.data.forEach(e => {
						const item = {}
						item.starid = e.star.id
						item.name = e.star.name
						item.avatar = e.star.head_img_s ? e.star.head_img_s : e.star.head_img_l
						item.hot = this.$app.formatNumber(e['hot'])
						rankList.push(item)
					})
					if (this.page == 1) {
						this.rankList = rankList
						this.$app.setData('index_rankList', this.rankList)
					} else {
						// 追加data
						this.rankList = this.rankList.concat(rankList)
					}
				} else {
					// 小时贡献榜
					if (this.page == 1) {
						this.rankList = res.data
					} else {
						this.rankList = this.rankList.concat(res.data)
					}
				}

			})
		},
	}
}
